<template>
    <section class="search">
        <h1>Track Player Stats</h1>
        <form @submit.prevent="onSubmit">
            <div class="form-group">
            <label for="platform">Platform</label>
             <select name="platform" id="platform" v-model="platform">
                <option value="psn">Playstation 4</option>
                <option value="xbl">X Box One</option>
                <option value="Origin"> PC </option>
            </select>
            </div>
            <div class="form-group">
                <label for="gamertag"> Gamertag</label>
                <input type="text" name="gamertag" id="gamertag" v-model="gamertag" placeholder="PSN ID, XBOX LIVE ID, PC ID, Etc">
            </div>
            <div class="form-group">
                <input type="submit" value="Submit" class="btn">
            </div>
            
        </form>
        
    </section>
</template>

<script>
export default {
    name:'Search',
    data(){
        return{
            platform: 'psn',
            gamertag: ''
        }
    },
    beforeCreate(){
        document.body.className = 'body-bg-image';
    },
    methods:{
        onSubmit(){
            if(!this.gamertag){
                this.$toasted.show("Gamertag must be entered",{
                    duration: 3000
                })
            }else{
                this.$router.push(`/profile/${this.platform}/${this.gamertag}`)
            }
        }
    }
}
</script>

