<template>
    <section>
        <div v-if="loading">
            <h3>Loading...</h3>
        </div>
        <div v-if="error">
            <h3>Oops error </h3>
            <router-link to="/">Go Back</router-link>
        </div>
        <div v-if="profileData" class="container">
            <h1 class="gamertag">
                <img :src="profileData.platformInfo.avatarUrl" alt="platform avatar" class="platform-avatar"/>
                {{profileData.platformInfo.platformUserId}}
            </h1>
            <div v-bind:key="legend" v-for="legend in legendData" class="legend-row">
            <div class="grid">
                <div>
                    <img :src="legend.metadata.imageUrl" alt="icon"/>
                </div>
                <div >
                    <ul>
                        <li>
                            <h4>Selected Legend:</h4>
                            <p>{{legend.metadata.name}}</p>
                        </li>
                        <li>
                            <h4>Legend Kills:</h4>
                            <p>{{legend.stats.kills.value}}</p>
                            <span>({{legend.stats.kills.percentile}}%)</span>
                        </li>
                  <!--
                        <li>
                            <h4>Season Wins:</h4>
                            <p>{{legend.stats.seasonWins.value}}</p>
                            ({{legend.stats.seasonWins.percentile}}%)
                        </li>
                        -->
                    </ul>
                    
                </div>
                
            </div>
            <hr/>
        </div>
        </div>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    name:'Profile',
    data(){
        return{
            loading: null,
            error: null,
            profileData: null,
            legendData: null
        }
    },
    beforeCreate(){
        document.body.className = 'body-bg-no-image'
    },
    async created(){
        this.loading = true;
        try{
            const response = await axios.get(`/api/v1/profile/${this.$route.params.platform}/${this.$route.params.gamertag}`)
            this.loading =false
            this.profileData = response.data.data;
            this.legendData = this.profileData.segments.filter(function(segement){
              if(segement.type !== "overview"){
                return segement;
              }
            })
            console.log(this.profileData)
            console.log(this.legendData)
        }catch(err){
            console.log(err)
        }
    }
}
</script>

